@import url(https://fonts.googleapis.com/css?family=Work+Sans:400,700);
body {
  margin: 0;
  padding: 0;
  font-family: 'Work Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: rgb(244,244,244) !important;
}

.mylink {
  color: rgb(51,51,51);
  text-decoration: underline;
  -webkit-text-decoration-color: rgb(60, 228, 149);
          text-decoration-color: rgb(60, 228, 149);
}

.mylink:hover {
  color: rgb(60, 228, 149);
  cursor: pointer;
}

.read-more {
  color: rgb(51,51,51);
  text-decoration: underline;
  -webkit-text-decoration-color: rgb(60, 228, 149);
          text-decoration-color: rgb(60, 228, 149);
  font-size: 20px;
  padding-top: 20px;
  cursor: pointer;
  display:inline-block;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;

}

.read-more:hover {
  color: rgb(60, 228, 149);
}

.header {
  font-size: 6.5vw;
  padding-top: 10vh;
}

.header-description {
  font-size: 20px;
  padding-top: 20px;
}

.projects {
  text-align: center;
  padding-top: 30px;
  font-size: 5.9vw;
  -webkit-text-decoration-color: rgb(60, 228, 149);
          text-decoration-color: rgb(60, 228, 149);
}

.work-heading {
  text-align: center; 
  padding-top: 30px;
  font-size: 5.9vw;
}

.work-heading-text {
  display:inline-block;
  cursor: pointer;
  text-decoration: underline;
  -webkit-text-decoration-color: rgb(60, 228, 149);
          text-decoration-color: rgb(60, 228, 149);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.work-heading-text:hover {
  color: rgb(60, 228, 149);
}

.work-image {
  max-width: 70%;
  font-size: 10vw;
  padding-top: 30px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.work-text {
  text-align: center;  
  margin-top: 30px;
}

@media only screen and (min-width : 992px) { 
  .header {
    padding-top: 10vh;
    font-size: 4vw;
  }
  
  .projects {
    font-size: 2.5vw;
  }

  .read-more {
    padding-top: 40px;
  }

  .work-heading {
    font-size: 2.5vw;
  }

  .header-description {
    text-align: left; 
    font-size: 20px;
    padding-top: 40px;
  }

  .work-text {
    text-align: left;
    font-size: 20px; 
    margin-top: 30px;
    border-left: 2px solid;
    border-color: rgb(60, 228, 149);
    padding-left: 10px;
  }

  .work-image {
    max-width: 70%;
    padding-top: 30px;
    float: right;
  }
}
